		Loadthepage() {
			var self = this;
			if(!self.loadshow){
				return
			}
			if (self.page < self.pages) {
				var page = self.page + 1;
				service.auth(self, service.api.index.direct_user , "POST", {
					page: page,
					limit: 15
				}, function(self, res) {
					console.log(res)
					self.pages = res.data.page_total;
					self.page = page;
					var list = res.data.data;
					list.forEach((v, i) => {
						self.scrolllist2.push(v);
					});
					if (self.pages == self.page || self.scrolllist2 == '') {
						self.loadshow = false
						uni.showToast({
							title: '数据已加载完毕',
							icon: 'none'
						});
					} else {
						self.loadshow = true
					}
				});
			} else {
				self.loadshow = false
				uni.showToast({
					title: '数据已加载完毕',
					icon: 'none'
				});
			}
		},
		lower(){
			this.Loadthepage();
		}
	},
	onShow() {
